                  </select>

                  <span v-show="form_data.bedrooms != '' && form_data.bedrooms != null" class="input-group-text bg-transparent border-left-0" style="border: 1px solid #eee">
                      <font-awesome-icon class="text-info" id="eye" :icon="['fa', 'check-circle']" />
                  </span>                   
                </div>
              </div>
            </div>                  

            <div class="col-md-2 col-sm-12 mt-4">
              <div class="form-row">
