                  <input type="text" class="form-control text-secondary" v-model="form_data.min_price" placeholder="Set minimum price.">
                  
                  <span v-show="form_data.min_price != '' && form_data.min_price != null" class="input-group-text bg-transparent border-left-0" style="border: 1px solid #eee">
