<script>
  import Breadcrumbs from '../../components/AutoBreadcrumbs'
  import Navbar from '../../components/Custom/PropertyNavbar'
  import MainFooter from '../../components/Footer'
  import Status from '../../components/Status'
  import NoData from '../../components/NoData'

  export default {
    name: 'Home',
    props: ['sale_type'],
    components: {
      Navbar,
      Breadcrumbs,
      MainFooter,
      Status,
      NoData
    }, 
    data() {
      return {
        title: {status: true, title: 'Your Data', actions: true},
        screen: 0,
        breadcrumbs_data: [
          {title: 'Home', link: '', active: false},
          {title: 'Properties', active: true},
        ],
        properties: {},
        size: 12,
        page: 0,
        totals: 0,
        form_data: {
          min_price: null,
          max_price: null,
          address: null,
          type: null,
          bedrooms: null,
          sale_type: this.sale_type
        },
        status_data: {
            "success": false,
            "success_message": 'Filter Successful!',
            "error": false,
            "error_message": 'There were some errors.',
            "loading": false
        },        
      } 
    },
    watch: {
      sale_type: function() {
        this.getProperties()
      }
    },
    computed: {
      page_props () {
        return this.$store.state.page_props
      }
    }, 
    mounted() {
      if(this.page_props.length == 0) {
        this.$store.dispatch("getProps");
      } 
            
      this.getProperties()
    },
    methods: {
      changeScreen(value) {
        this.screen = value
      },
      getProperties() {
        this.status_data.loading = true

        this.axios.get('api/property/filter/sale_type?sale_type=' + this.sale_type).then((response) => {
          this.status_data.loading = false
          this.properties = response.data.data
          alert(this.properties.length)
          this.totals = response.data.totals
        }).catch((error) => {
          this.properties = []
          this.status_data.loading = false
          console.log(error)
        })
      },
      filterProperties() {
        this.status_data.loading = true

        this.axios.post('api/filter-property?size=' + this.size + '&page=' + this.page + '', this.form_data).then((response) => {
          this.status_data.loading = false
          this.properties = response.data.data
          this.totals = response.data.totals
          document.querySelector('.property').scrollIntoView({behavior: "smooth"})
        }).catch((error) => {
          this.status_data.loading = false
          document.querySelector('.property').scrollIntoView({behavior: "smooth"}) 
          console.log(error)
        })
      },
      next() {
        if((this.page*this.size) < this.totals) {
          this.page = this.page + 1
          this.filterProperties()
        }
      },
      previous() {
        if(this.page >= 0) {
          this.page = this.page - 1
          this.filterProperties()
        }
      }      
    }
  }
</script>
